import React, { Component } from 'react';
import './App.css';
import axios from 'axios';
import { connect } from 'react-redux';
import { Form, Input, Button, Row, Col, Switch, message as Message, } from 'antd'
const FormItem = Form.Item;

class BlogCateEdit extends Component {
    constructor(props) {
        super(props);
        const createNew = this.props.match.path === '/cate/new';
        this.state = {
            loading: !createNew,
            isCreatingNewCate: createNew,
            cate: {
                cid: this.props.match.params.id,
                title: '',
                parent: '',
                isEnable: false,
                iorder: 0,
                href: '',
                link:'',
            },
        };

        this.formRef = React.createRef();
    }

    componentDidMount() {
        const that = this;
        if (!that.state.isCreatingNewCate) {
            axios.get('/api/cate/' + that.state.cate.cid).then(function (res) {
                if (res.data.status) {
                    res.data.category.isEnable = res.data.category.isEnable === 1;
                    that.setState({ cate: res.data.category, }, () => { that.formRef.current.resetFields(); });
                } else {
                    Message.error(res.data.message);
                }
            }).catch(function (err) {
                Message.error(err.message);
            })
            .finally(() => {
                this.setState({ loading: false });
            });
        }
    }

    onValuesChange = (changedValues, allValues) => {
        let cate = { ...this.state.cate };
        for (let key in changedValues) {
            if (changedValues.hasOwnProperty(key)) {
                cate[key] = changedValues[key];
            }
        }
        this.setState({ cate });
    };

    submitData = async () => {
        const cate = this.state.cate;
        const res = this.state.isCreatingNewCate ? await axios.post(`/api/cate/new`, cate) : await axios.put(`/api/cate`, cate);
        const { status, message } = res.data;
        debugger;
        if (status) {
            Message.success('');
            this.props.history.goBack();
        } else {
            Message.error(message);
        }
    };

    render() {
        return (
            <div className={'content-area'}>
                <h1 className='mb-20'></h1>
                <Form className='mlr-25' onValuesChange={this.onValuesChange} ref={this.formRef} initialValues={this.state.cate}>
                    <Row>
                        <Col span={11}>
                            <FormItem name='title' label='' required message='' >
                                <Input size='middle' placeholder="" width='100%' />
                            </FormItem>
                        </Col>
                        <Col span={11}>
                            <FormItem name='parent' label='' required >
                                <Input size='middle' placeholder="" />
                            </FormItem>
                        </Col>
                    </Row>
                    <Row>
                        <Col span={11}>
                            <FormItem name='isEnable' label='' required valuePropName="checked">
                                <Switch checked={this.state.cate.isEnable} />
                            </FormItem>
                        </Col>
                        <Col span={11}>
                            <FormItem name='iorder' label='' required >
                                <Input size='middle' placeholder="" />
                            </FormItem>
                        </Col>
                    </Row>
                    <Row>
                        <Col span={11}>
                            <FormItem name='href' label='' required>
                                <Input size='middle' placeholder="" readOnly={!this.state.isCreatingNewCate} />
                            </FormItem>
                        </Col>
                        <Col span={11}>
                            <FormItem name='link' label='' required>
                                <Input size='middle' placeholder=""  />
                            </FormItem>
                        </Col>
                    </Row>
                    <Row className='pl-20'>
                        <Col span={18}></Col>
                        <Col span={4}>
                            <FormItem style={{ 'text-align': 'right;' }} >
                                <Button size="middle" type="primary" htmlType="submit" onClick={this.submitData}>  </Button>
                            </FormItem>
                        </Col>
                    </Row>
                </Form>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return state;
};
export default connect(mapStateToProps)(BlogCateEdit);
