import React, { Component } from 'react';

import { connect } from 'react-redux';

import axios from 'axios';
import { message as Message, Button, Tabs, Form, Input } from 'antd';

const { TabPane } = Tabs;

const tabs = [
  {
    label: '',
    settings: [
      {
        key: 'domain',
        description: 'www.domain.com',
      },
      {
        key: 'language',
        description: '',
      },
      {
        key: 'copyright',
        description: ' HTML ',
      },
      {
        key: 'allow_comments',
        description: 'true  false',
      },
      {
        key: 'use_cache',
        description: 'true  false',
      },
    ],
  },
  {
    label: '',
    settings: [
      {
        key: 'theme',
        description:
          "bulma, bootstrap, bootstrap5, v2ex, next  w3",
      },
      {
        key: 'code_theme',
        description:
          'https://www.jsdelivr.com/package/npm/highlight.js?path=styles',
      },
      {
        key: 'site_name',
        description: "",
      },
      {
        key: 'description',
        description: '',
      },
      {
        key: 'nav_links',
        description: ' JSON ',
        isBlock: true,
      },
      {
        key: 'author',
        description: '',
      },
      {
        key: 'motto',
        description: '',
      },
      {
        key: 'favicon',
        description: '',
      },
      {
        key: 'brand_image',
        description: '',
      },
      {
        key: 'index_page_content',
        description: ' HTML  404 ',
        isBlock: true,
      }
    ],
  },
  {
    label: '',
    settings: [
      {
        key: 'ad',
        description: '',
        isBlock: true,
      },
      {
        key: 'extra_header_code',
        description: ' header ',
        isBlock: true,
      },
      {
        key: 'extra_footer_code',
        description: ' footer ',
      },
      {
        key: 'disqus',
        description: 'Disqus ',
      },
      {
        key: 'extra_footer_text',
        description: ' HTML',
      },
      {
        key: 'message_push_api',
        description:
          ' API https://github.com/songquanpeng/message-pusher',
      },
    ],
  },
];

class Settings extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      submitLoading: false,
      language: 'javascript',
      options: {},
      optionIndex: 0,
    };
  }

  static getDerivedStateFromProps({ status }) {
    return { status };
  }

  async componentDidMount() {
    if (this.state.status === 0) {
      Message.error('');
      this.props.history.push('/login');
      return;
    }
    await this.fetchData();
  }

  fetchData = async () => {
    try {
      this.setState({ loading: true });
      const res = await axios.get(`/api/option/`);
      let { status, message, options } = res.data;
      let temp = {};
      if (status) {
        options.forEach((option) => {
          temp[option.key] = option.value;
        });
        options = temp;
        console.log(options);
        this.setState({ options });
      } else {
        Message.error(message);
      }
      this.setState({ loading: false });
    } catch (e) {
      Message.error(e.message);
    }
  };

  updateOption = (key, value) => {
    let options = this.state.options;
    options[key] = value;
    this.setState({ options });
  };

  submit = async () => {
    let options = this.state.options;
    try {
      const res = await axios.put(`/api/option/`, options);
      const { status, message } = res.data;
      if (status) {
        Message.success('');
      } else {
        Message.error(message);
      }
      this.setState({ loading: false });
    } catch (e) {
      Message.error(e.message);
    } finally {
      this.setState({ submitLoading: false });
    }
  };

  render() {
    return (
      <div className={'content-area'}>
        <h1></h1>
        <div style={{ background: '#fff', padding: 16 }}>
          <Tabs tabPosition={'left'}>
            {tabs.map((tab) => {
              tab.settings.sort((a, b) => {
                if (a.key < b.key) {
                  return -1;
                }
                if (a.key > b.key) {
                  return 1;
                }
                return 0;
              });
              return (
                <TabPane tab={tab.label} key={tab.label}>
                  <Form layout={'vertical'}>
                    {tab.settings.map((setting) => {
                      setting.label = setting.key
                        .replaceAll('_', ' ')
                        .toUpperCase();
                      return (
                        <Form.Item
                          label={setting.label ? setting.label : setting.key}
                        >
                          {setting.isBlock ? (
                            <Input.TextArea
                              placeholder={setting.description}
                              value={this.state.options[setting.key]}
                              onChange={(e) => {
                                this.updateOption(setting.key, e.target.value);
                              }}
                              rows={10}
                            />
                          ) : (
                            <Input
                              placeholder={setting.description}
                              value={this.state.options[setting.key]}
                              onChange={(e) => {
                                this.updateOption(setting.key, e.target.value);
                              }}
                            />
                          )}
                        </Form.Item>
                      );
                    })}
                    <Button type="primary" onClick={() => this.submit()}>
                      
                    </Button>
                  </Form>
                </TabPane>
              );
            })}
          </Tabs>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return state;
};

export default connect(mapStateToProps)(Settings);
